.task-form {
  margin: 40px 0 10px; }
  @media screen and (min-width: 33.75em) {
    .task-form {
      margin: 80px 0 20px; } }

.task-form__input {
  outline: none;
  border: 0;
  border-bottom: 1px dotted #666;
  border-radius: 0;
  padding: 0 0 5px 0;
  width: 100%;
  height: 50px;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 300;
  color: #fff;
  background: transparent;
  -webkit-font-smoothing: antialiased; }
  @media screen and (min-width: 33.75em) {
    .task-form__input {
      height: 61px;
      font-size: 2rem; } }
  .task-form__input::-webkit-input-placeholder {
    color: #999;
    opacity: 1; }
  .task-form__input:-ms-input-placeholder {
    color: #999;
    opacity: 1; }
  .task-form__input::placeholder {
    color: #999;
    opacity: 1; }
  .task-form__input:focus::-webkit-input-placeholder {
    color: #777;
    opacity: 1; }
  .task-form__input:focus:-ms-input-placeholder {
    color: #777;
    opacity: 1; }
  .task-form__input:focus::placeholder {
    color: #777;
    opacity: 1; }
  .task-form__input::-ms-clear {
    display: none; }



